@import url('https://fonts.googleapis.com/css2?family=Spartan:wght@500;600;700&display=swap');

:root{

}


body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  font-family: 'Spartan', sans-serif;
  color: rgb(1, 0, 36);
  line-height: 1.4;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

body::-webkit-scrollbar{
  width: 0.25rem;
}

body::-webkit-scrollbar-track{
  background:rgb(198, 223, 255)
}

body::-webkit-scrollbar-thumb{
  background:rgb(0, 14, 31)
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loading{
  height: 100vh;
    width: 100%;
    margin: 0;
    padding: 0;
}

.background{
  position: fixed;
  z-index: 3;
  background-color: rgb(0, 14, 31);
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.PopUp{
  position: fixed;
  z-index: 4;
  margin: none;
  margin-bottom: 4rem;
  justify-content: center;
  align-items: center;
  overflow: auto;

}

.logo{
  justify-content: center;
  width:46%;
  height: auto;
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-width: 20rem;
  max-height: 28rem;
  
}

.description{
  display: flex;
  align-items: center;
  justify-content: center;
  order: 1;
  width: 80%;
  height: auto;
  font-size: 2.6vh;
  color: white;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
margin-bottom: 4vh;
  max-width: 18em;
}

.loader{
  z-index: 5;
  color: rgb(0, 37, 80);
  justify-content: center;
  text-align: center;
  padding: 1vh;
  width: 80%;
  height: 4vh;
  border-radius: 1vh;
  margin: auto;
  order: 2;
}

.quickLink{
  position: fixed;
  color: aliceblue;
  text-align: center;
  margin: auto;
  bottom:0%;
  right: 0%;
  left: 0%;
  font-weight: bold;
  width: 100%;
  height: auto;
  background-color: rgb(0, 14, 31);
  padding: 1vh 0 1vh 0;
}

.quickLinkIcons{
  display: flex;
  position: relative;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  max-width: 36rem;
  margin: auto;
}

.quickLinkIcon{
  position: relative; 
  width: 33%;
  height: auto;
  max-width: 20rem;
  margin: 1vh;
  margin-top: 0%;
  margin-bottom: 3vh;
  color: rgb(255, 145, 213);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: max(2vh, 16px);
}